import PageButton from '../../components/button.vue';
import EmptyView from '../../components/emptyview.vue';

export default {
	components: {
		PageButton,
		EmptyView
	},
	data() {
		return {
			bankList: [],
			page: 1,
			size: 10,
			totalPages: 1
		}
	},
	onShow() {
		let that = this;
		that.bankList = [];
		that.page = 1;
		that.size = 10;
		that.totalPages = 1;
		that.getBankList();
	},
	onLoad() {

	},
	onPullDownRefresh() {
		// 增加下拉刷新数据的功能
		var self = this;
		self.getBankList();
	},
	methods: {
		getBankList: function() {

			let that = this;
			if (that.totalPages <= (that.page - 1) * that.size) {
				return;
			}
			var param = {};
			param.pageNum = that.page;
			param.pageSize = that.size;
			that.$util.request({
				url: "/didi-doctor/doctorbankinfo/queryPage",
				param: param,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					var data = res.data;
					if (data == null) {
						return;
					}
					that.bankList = that.bankList.concat(data.list);
					that.page = data.pageNum + 1;
					that.totalPages = data.total;
				},
				error: function() {}
			})
		},
		jumpToAddBank: function() {
			uni.navigateTo({
				url: '/pages/my/userBankAdd'
			})
		},
		deleteBank:function(bankId){
			let that = this;
			var param = {};
			param.bankId = bankId;
			that.$util.request({
				url: "/didi-doctor/doctorbankinfo/delete",
				param: param,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.showToast({
						title: '删除成功',
						icon: 'success',
						duration: 2000
					})
					setTimeout(function() {
						that.bankList = [];
						that.page = 1;
						that.size = 10;
						that.totalPages = 1;
						that.getBankList();
					}, 500)
				
				},
				error: function() {}
			})
		}
	}
}
